@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
  transition: all 0.2s linear;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container .data .profile {
  font-size: 2rem;
  background-color: black;
  padding: 2rem;
  border-radius: 2rem;
}
.container .data .profile:hover {
  background-color: darkkhaki;
}

.container .data .profile .image {
  text-align: center;
}

.container .data .profile .image img {
  border-radius: 50%;
}

.container .data .profile p {
  margin-top: 3rem;
  color: wheat;
  text-align: center;
}


html {
  font-size: 62.5%;
  background-color: white;
}

header {
  font-size: 1.7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  size: 10px;

  box-shadow: 0 0.5rem 1.5rem rgba(241, 239, 239, 0.178);
  background-color: darkcyan;
  margin-bottom: 5px;
  width: 100%;
}

header .logo {
  color: navy;
  font-size: 4rem;
  font-weight: bolder;
  padding: 12px;
  position: static;
}

header .navbar a {
  color: black;
  height: 97px;
}

.UserCard h1 {
  font-size: 2.5rem;
  size: 10px;
}

.UserCard {
  size: 1033px 97px;
  height: 97px;
}

.UserCard button {
  font-size: 2rem;
  padding: 10px 50px;
  border-radius: 0.5rem;
  border: 0.3rem solid rgba(0, 0, 0, 0.3);
  margin: 2rem 5rem;
}

.UserCard button:hover {
  background-color: darkkhaki;
  color: beige;
  cursor: pointer;
}
.loading {
  padding-bottom: 100%;
}
.container .data {
  display: grid;
  grid-template-columns: auto auto auto;
  padding: 90px 10%;
  grid-gap: 50px;
  gap: 50px;
  width: 100%;
}


@media (max-width: 600px) {
  .container,
  .UserCard {
    max-width: 540px;
  }
}

@media (max-width: 2px) {
  html {
    font-size: 50%;
  }
}

